import { Injectable }   from "@angular/core";

@Injectable()
export class UtilityService {
    private static _instance:UtilityService = new UtilityService();

    constructor() {
        if(UtilityService ._instance) {
            throw new Error("The Logger is a singleton class and cannot be created!");
        }

        UtilityService._instance = this;
    }

    public static getInstance() : UtilityService {
        return UtilityService._instance;
    }


    public generateUUID(){
        // return "00000000-0000-0000-0000-000000000000";
        // http://www.ietf.org/rfc/rfc4122.txt
        let s:any = [];
        let hexDigits = "0123456789abcdef";
        for (let i = 0; i < 36; i++) {
            s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
        }
        s[14] = "4";  // bits 12-15 of the time_hi_and_version field to 0010
        s[19] = hexDigits.substr(s[19] & 0x3 | 0x8, 1);  // bits 6-7 of the clock_seq_hi_and_reserved to 01
        s[8] = s[13] = s[18] = s[23] = "-";
        return s.join("");
    }

    // ...
}


